<template>
  <div class="login">
    <!-- logo头部标签 -->
    <van-nav-bar right-text="关闭" @click-right="close">
      <template #left>
        <img src alt />
        <h2>Luckin Coffee</h2>
      </template>
    </van-nav-bar>

    <!-- text文本 -->
    <h1>欢迎回来!</h1>
    <p>Please login to your</p>
    <p>accounts</p>

    <!-- 登录表单 -->
    <van-form>
      <van-field name="手机号" placeholder="手机号" v-model="userLoginInfo.phone" maxlength="11" />
      <van-field type="password" name="密码" placeholder="密码" v-model="userLoginInfo.password">
        <template #extra>
          <div class="forgetpwd" @click="forgetPwd">忘记密码?</div>
        </template>
      </van-field>
      <div class="btn">
        <van-button
          block
          type="info"
          size="large"
          color="#0b34ba"
          native-type="submit"
          @click="login"
        >登录</van-button>
      </div>
    </van-form>

    <!-- 注册 -->
    <div class="register">
      <span @click="register">注册</span>
    </div>

    <!-- 注册弹出层 -->
    <van-popup v-model="show" closeable round position="bottom">
      <h1>注册</h1>

      <div class="register-box">
        <van-form>
          <van-field
            name="手机号"
            placeholder="手机号 中国 (+86)"
            v-model="userRegisterInfo.phone"
            maxlength="11"
          />
          <van-field name="昵称" placeholder="昵称" v-model="userRegisterInfo.nickName" />
          <van-field
            type="password"
            name="密码"
            placeholder="密码"
            v-model="userRegisterInfo.password"
          />
          <div class="btn">
            <van-button
              block
              type="info"
              size="large"
              color="#0b34ba"
              native-type="submit"
              @click="userRegister"
            >注册</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/login.less";
import { validForm } from "../assets/js/validForm";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      show: false,

      userLoginInfo: {
        phone: "",
        password: "",
      },

      userRegisterInfo: {
        phone: "",
        nickName: "",
        password: "",
      },
    };
  },

  methods: {
    // 关闭
    close() {
      this.$router.push({name: 'Home'})
    },

    // 显示注册弹出层
    register() {
      this.show = true;
    },

    // 用户注册
    userRegister() {
      let userInfo = this.userRegisterInfo;

      let options = {
        testPhone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errMsg: "手机号错误",
        },
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errMsg: "请确认昵称格式为1-10位汉字或字母",
        },
        testPwd: {
          value: userInfo.password,
          reg: /^[\d\w]{6,16}$/,
          errMsg: "请确认密码格式为6-16位数字或字母",
        },
      };

      // 验证不通过直接返回
      if (!validForm.verify(options)) {
        return;
      }

      // 格式正确，发起注册请求
      // 开启加载提示
      this.$toast.loading({
        message: "注册中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      // 序列化参数
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);
      // 

      // post请求
      this.axios({
        method: "POST",
        url: "/register",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          // 

          if (result.data.code == 100) {
            this.$toast.success(result.data.msg);
            this.show = false;
            for (let key in userInfo) {
              userInfo[key] = "";
            }
          } else {
            this.$toast.fail(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 用户登录
    login() {
      let userInfo = this.userLoginInfo;

      let options = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errMsg: '账号密码格式错误'
        },
        password: {
          value: userInfo.password,
          reg: /^[\w\d]{6,16}$/,
          errMsg: '账号密码格式错误'
        }
      };

      // 账号密码格式错误直接返回
      if (!validForm.verify(options)) {
        return;
      }

      // 账号密码格式正确
      // 开启加载提示
      this.$toast.loading({
        message: '连线中...',
        forbidClick: true,
        duration: 0,
        loadingType: 'spinner'
      })

      // 参数序列化
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);

      // post登录请求
      this.axios({
        method: 'POST',
        url: '/login',
        data
      }).then(result => {
        // 
        this.$toast.clear();

        if (result.data.code == 200) {
          // 登录成功执行
          localStorage.setItem('owner', result.data.token);
          this.$router.push({name: 'Home'});
        } else {
          // 登录失败则执行
          this.$toast.fail(result.data.msg);
        }
      }).catch(err => {
        this.$toast.clear();
      })
    },

    // 用户忘记密码
    forgetPwd() {
      this.$router.push({name: 'Forget'})
    },
  },
};
</script>